.about {
  background-color: #fff5f6;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  padding-top: 125px;

  @media screen and (max-width: 767px) {
    background-image: url('../images/milk-mob.png');
    background-size: contain;
    background-position: right 0 bottom 61px;
    background-repeat: no-repeat;

    @media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
      background-image: url('../images/milk-mob-2x.png');
      background-size: contain;
      background-position: right 0 bottom 61px;
      background-repeat: no-repeat;
    }
  }
  @media screen and (min-width: 768px) {
    padding-top: 130px;
  }
  @media screen and (min-width: 1280px) {
    padding-top: 125px;
    padding-bottom: 39px;

    background-image: url('../images/milk-desc.png');
    background-size: contain;
    background-position: right 0 bottom -59px;
    background-repeat: no-repeat;

    @media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
      background-image: url('../images/milk-desc-2x.png');
      background-size: contain;
      background-position: right 0 bottom 61px;
      background-repeat: no-repeat;
    }
  }

  &__label {
    margin-bottom: 15px;

    font-family: Titan One, sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 1.17;
    text-align: center;
    text-transform: uppercase;

    color: var(--pretitle-section-color);

    @media screen and (min-width: 768px) {
      margin-bottom: 16px;
      font-size: 26px;
      line-height: 1.15;
    }
    @media screen and (min-width: 1280px) {
      margin-bottom: 15px;
      font-size: 30px;
      line-height: 1.13;
      letter-spacing: 0.04em;
    }
  }

  &__title {
    margin-bottom: 26px;

    font-family: Titan One;
    font-style: normal;
    font-weight: 400;
    font-size: 34px;
    line-height: 1.15;
    text-align: center;
    letter-spacing: 0.04em;
    text-transform: uppercase;

    color: var(--title-section-color);
    @media screen and (min-width: 768px) {
      margin-bottom: 50px;
      font-size: 48px;
      line-height: 1.15;
    }
    @media screen and (min-width: 1280px) {
      margin-bottom: 55px;
      font-size: 58px;
      line-height: 1.14;
      letter-spacing: 0.06em;
    }
  }
}

.wrapper {
  @media screen and (min-width: 768px) {
    display: flex;
  }
}

.wrapper__left {
  @media screen and (min-width: 768px) {
    margin-left: 17px;
  }
  @media screen and (min-width: 1280px) {
    margin-left: 74px;
  }
}

.about__img {
  display: block;
  width: 100%;
  height: auto;
  max-width: 280px;
  margin-left: auto;
  margin-right: auto;
  @media screen and (min-width: 768px) {
    min-width: 340px;
  }
  @media screen and (min-width: 1280px) {
    min-width: 516px;
  }
}

.about__text {
  padding-left: 30px;
  padding-right: 30px;
  @media screen and (min-width: 768px) {
    padding: 0;
  }

  &--black {
    margin-top: 46px;
    margin-bottom: 30px;

    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 1.62;
    color: var(--made-first-text-color);

    @media screen and (min-width: 768px) {
      margin-top: 50px;

      font-size: 14px;
      line-height: 1.79;
    }
    @media screen and (min-width: 1280px) {
      font-size: 16px;
      line-height: 1.76;
      letter-spacing: 0.04em;
    }
  }
  &--gray {
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 1.62;
    color: var(--made-second-text-color);

    @media screen and (min-width: 768px) {
      font-size: 14px;
      line-height: 1.79;
      letter-spacing: 0.04em;
    }
    @media screen and (min-width: 1280px) {
      font-size: 14px;
      line-height: 1.93;
    }
  }
}

.about__btn {
  margin-top: 17px;
  margin-left: 30px;
  width: 176px;
  height: 44px;
  display: flex;
  align-items: center;
  justify-content: center;

  font-style: normal;
  font-weight: 700;
  font-size: 14px;
  line-height: 1.29;
  text-align: center;

  color: var(--button-text-color);
  background-color: #fff;
  border: none;
  border-radius: 22px;
  cursor: pointer;
  @media screen and (min-width: 768px) {
    margin-top: 62px;
    margin-left: 0;

    font-size: 16px;
    line-height: 1.31;
  }
  @media screen and (min-width: 1280px) {
    margin-top: 45px;
    font-size: 16px;
    line-height: 1.31;
  }

  &:hover,
  &:focus {
    background-color: #ecebeb;
  }
}

.btn__icon {
  margin-left: 14px;
}
